  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    #t_3_1
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h1 3.1.	Metodologías de interacción y participación en animación turística

    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La animación turística en contextos étnicos debe diseñarse bajo un enfoque diferencial que fomente la participación genuina y evite procesos de folklorización, exotización o simplificación cultural. Estas metodologías se enmarcan en el respeto por la autonomía de los pueblos y la salvaguardia de sus expresiones culturales, tal como lo establecen instrumentos normativos como el Convenio 169 de la OIT y la Política de Turismo Sostenible de Colombia (MINCIT, 2022).
      p A continuación, se indican cuáles son las metodologías de interacción y participación, así mismo se indican cuáles son los principios a tener en cuenta para su aplicación:

    br
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema3/tema1/tema31-tabla15.svg', alt='Texto que describa la imagen')
      

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Técnicas como la “ronda de la palabra”, inspirada en prácticas deliberativas comunitarias, o el “juego de roles basado en narrativas ancestrales validadas”, permiten a los visitantes aproximarse a los valores culturales de manera vivencial y respetuosa. En este contexto, el rol del guía se redefine como el de un facilitador intercultural, cuyo objetivo es mediar el encuentro y propiciar un diálogo horizontal donde el conocimiento local ocupe una posición central y protagónica.
      p La implementación técnica de estas metodologías requiere una planeación estructurada que asegure su pertinencia y efectividad. La “ronda de la palabra” debe ser guiada mediante preguntas detonantes previamente validadas con la comunidad, evitando interrogantes que puedan invadir la privacidad o profundizar en conocimientos sagrados. Por su parte, el “juego de roles” debe construirse a partir de narrativas autorizadas, utilizando elementos simbólicos y no réplicas exactas de objetos ceremoniales, para evitar apropiación cultural o falta de respeto.
      p A continuación se indican las fases que deben ejecutarse en los protocolos específicos de implementación técnica de las metodologías de interacción y participación:

    br
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema3/tema1/tema31-rec29.svg', alt='Texto que describa la imagen')

    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p El guía, en su función de facilitador, debe poseer competencias comunicativas para gestionar las intervenciones, garantizando que todas las voces sean escuchadas y que se mantenga un ambiente de respeto mutuo, alineándose así con los principios de la interpretación del patrimonio, que buscan provocar reflexión y no solo entretenimiento.
      p La evaluación del impacto de estas metodologías se realiza mediante la observación de indicadores cualitativos, como el nivel de compromiso reflexivo del público, la calidad de las preguntas que surgen y el respeto demostrado hacia los espacios y narrativas. La correcta aplicación de estas técnicas no solo enriquece la experiencia del visitante, sino que fortalece la gobernanza turística local, posicionando a la comunidad como gestora de su propio patrimonio. Esta aproximación metodológica se consolida como una mejor práctica en el turismo étnico y sostenible, contribuyendo a la vez a la competitividad del destino y a la conservación de la diversidad biocultural.
      .col-md-11
        p Los indicadores que permiten realizar la evaluación de impacto son:
        .p •	Grado de comprensión intercultural alcanzado.
        .p •	Nivel de respeto demostrado hacia los protocolos comunitarios.
        .p •	Calidad de las interacciones establecidas.
        .p •	Contribución al fortalecimiento de la gobernanza local.
        .p •	Satisfacción mutua (visitantes y comunidad anfitriona).
    
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p A continuación, se presenta un ejemplo técnico en dónde se aprecia la aplicación de la verificación:

