    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Esta aproximación metodológica se consolida como una mejor práctica en el turismo étnico y sostenible, contribuyendo a la competitividad del destino y a la conservación de la diversidad biocultural.


