ento local ocupe una posición central y protagónica.")]),_c('p',[_vm._v("La implementación técnica de estas metodologías requiere una planeación estructurada que asegure su pertinencia y efectividad. La “ronda de la palabra” debe ser guiada mediante preguntas detonantes previamente validadas con la comunidad, evitando interrogantes que puedan invadir la privacidad o profundizar en conocimientos sagrados. Por su parte, el “juego de roles” debe construirse a partir de narrativas autorizadas, utilizando elementos simbólicos y no réplicas exactas de objetos ceremoniales, para evitar apropiación cultural o falta de respeto.")]),_c('p',[_vm._v("A continuación se indican las fases que deben ejecutarse en los protocolos específicos de implementación técnica de las metodologías de interacción y participación:")])]),_c('br'),_c('div',{staticClass:"row justify-content-center",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-9"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema1/tema31-rec29.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('br'),_c('div',{staticClass:"row justify-content-center align-items-center mb-4",attrs:{"data-aos":"flip-up"}},[_c('p',[_vm._v("El guía, en su función de facilitador, debe poseer competencias comunicativas para gestionar las intervenciones, garantizando que todas las voces sean escuchadas y que se mantenga un ambiente de respeto mutuo, alineándose así con los principios de la interpretación del patrimonio, que buscan provocar reflexión y no solo entretenimiento.")]),_c('p',[_vm._v("La evaluación del impacto de estas metodologías se realiza mediante la observación de indicadores cualitativos, como el nivel de compromiso reflexivo del público, la calidad de las preguntas que surgen y el respeto demostrado hacia los espacios y narrativas. La correcta aplicación de estas técnicas no solo enriquece la experiencia del visitante, sino que fortalece la gobernanza turística local, posicionando a la comunidad como gestora de su propio patrimonio. Esta aproximación metodológica se consolida como una mejor práctica en el turismo étnico y sostenible, contribuyendo a la vez a la competitividad del destino y a la conservación de la diversidad biocultural.")]),_c('div',{staticClass:"col-md-11"},[_c('p',[_vm._v("Los indicadores que permiten realizar la evaluación de impacto son:")]),_c('div',{staticClass:"p"},[_vm._v("• Grado de comprensión intercultural alcanzado.")]),_c('div',{staticClass:"p"},[_vm._v("• Nivel de respeto demostrado hacia los protocolos comunitarios.")]),_c('div',{staticClass:"p"},[_vm._v("• Calidad de las interacciones establecidas.")]),_c('div',{staticClass:"p"},[_vm._v("• Contribución al fortalecimiento de la gobernanza local.")]),_c('div',{staticClass:"p"},[_vm._v("• Satisfacción mutua (visitantes y comunidad anfitriona).")])])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-4",attrs:{"data-aos":"flip-up"}},[_c('p',[_vm._v("A continuación, se presenta un ejemplo técnico en dónde se aprecia la aplicación de la verificación:")])]),_c('div',{staticClass:"row justify-content-center",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"col-lg-8"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema3/tema1/tema31-rec30.svg"),"alt":"Texto que describa la imagen"}})])])]),_c('br'),_c('div',{staticClass:"row justify-content-center align-items-center mb-4",attrs:{"data-aos":"flip-up"}},[_c('p',[_vm._v("Esta aproximación metodológica se consolida como una mejor práctica en el turismo étnico y sostenible, contribuyendo a la competitividad del destino y a la conservación de la diversidad biocultural.")])])])
